/**
 * @module string
 * 字符串工具类
 */

/**
 * 使用*遮蔽字符串
 * @param   {String} cc 要格式化的字符串
 * @param   {Number} num 指定替换从字符串开始到末尾的所有项,使用负值从字符串的尾部替换元素
 * @param   {String} character 指定的掩码字符
 * @return  {String} 格式化后的字符串
 */
export const mask = (
  cc: string,
  num: number = 4,
  character: string = '*'
): string => `${cc}`.slice(-num).padStart(`${cc}`.length, character);
