/**
 * @module string
 * 字符串工具类
 */

/**
 * 驼峰转自定义连字符
 * @param   {String} str 要格式化的字符串
 * @param   {String} separator: 自定义连字符
 * @return  {String} 格式化后的字符串
 */
export const fromCamelCase = (str: string, separator: string = '_'): string =>
  str &&
  str
    .replace(/([a-z\d])([A-Z])/g, '$1' + separator + '$2')
    .replace(/([A-Z]+)([A-Z][a-z\d]+)/g, '$1' + separator + '$2')
    .toLowerCase();
