/**
 * @module string
 * 字符串工具类
 */

/**
 * 首字母小写
 * @param   {String} str 要格式化的字符串
 * @param   {Boolean} upperRest 是否仅第一个单词首字母小写
 * @return  {String} 格式化后的字符串
 */
export const decapitalize = (
  [first, ...rest]: string,
  upperRest: boolean = false
): string =>
  first.toLowerCase() +
  (upperRest ? rest.join('').toUpperCase() : rest.join(''));
