/**
 * @module string
 * 字符串工具类
 */

/**
 * 首字母大写
 * @param   {String} str 要格式化的字符串
 * @param   {Boolean} lowerRest 是否仅第一个首字母大写
 * @return  {String} 格式化后的首字母大写字符串
 */
export const capitalize = (
  [first, ...rest]: string,
  lowerRest: boolean = false
): string =>
  first.toUpperCase() +
  (lowerRest ? rest.join('').toLowerCase() : rest.join(''));
