/**
 * @module string
 * 字符串工具类
 */

/**
 * 获取URL参数
 * @param   {String} url 要格式化的URL
 * @return  {Object} 格式化后的参数Josn对象
 */
export const getURLParameters = (url: string): object =>
  (url.match(/([^?=&]+)(=([^&]*))/g) || []).reduce(
    (a, v) => (
      (a[v.slice(0, v.indexOf('='))] = v.slice(v.indexOf('=') + 1)), a
    ),
    {}
  );
