/**
 * @module string
 * 字符串工具类
 */

/**
 * 对象转字符串
 * @param   {Object} obj 要格式化的对象
 * @return  {String} 格式化后的字符串
 */
export const toQs = (obj: object): string => {
  const str = [];
  for (const p in obj) {
    if (obj.hasOwnProperty(p) && obj[p]) {
      str.push(encodeURIComponent(p) + '=' + encodeURIComponent(obj[p]));
    }
  }
  return str.join('&');
};
