/**
 * @module string
 * 字符串工具类
 */

/**
 * 字符串转连字符('_')
 * @param   {String} str 要格式化的字符串
 * @return  {String} 格式化后的连字符
 */
export const toSnakeCase = (str: string): string =>
  str &&
  str
    .match(
      /[A-Z]{2,}(?=[A-Z][a-z]+[0-9]*|\b)|[A-Z]?[a-z]+[0-9]*|[A-Z]|[0-9]+/g
    )!
    .map((x: string) => x.toLowerCase())
    .join('_');
